export default {
	data() {
		return {
			// back: 'http://192.168.1.110:3000/image/1581851328227.jpg',
			current: 0,
			now_page: 1,
			size: 10,
			info: {},
			post: [],
			api: this.$API,
			total: '' 
		}
	},
	onLoad() {
		this.getList();
		this.getUserInfo();
	},
	methods: {
		getList() { 
			let account = uni.getStorageSync('account');
			this.$http.get('/User/myAlbum', {
					// index: this.now_page, 
					// size: this.size 
				})
				.then(res => {
					this.post = res.data.list;
					// this.total = res.data.total;
					for (let i = 0; i < this.post.length; i++) {
						for (let j = 0; j < this.post[i].likeList.length; j++) {
							if (this.post[i].likeList[j].uid == account) {
								this.post[i].islike = 1;
							}
						}
					} 
					console.log(this.post);
				})
				.catch(err => {
					uni.showToast({
						icon: 'none',
						title: '系统错误，请稍后再试'
					}) 
				})
		},
		getUserInfo() {
			this.$http.get('/User/userInfo')
				.then(res => {
					this.info = res.data;
				})
				.catch(err => {
					uni.showToast({
						icon: 'none',
						title: '请重新登录'
					})
				})
		},
	}
}
